.weather {
  padding-top: 100px;
  margin: 0 auto;
  max-width: 400px;
  &__inner {
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    position: relative;
    z-index: -1;
  }

  &__main {
    color: #fff;
    text-shadow: 1px 2px 2px #333;
  }

  &__info {
    font-size: 32px;
    font-weight: 300;
    display: flex;
    align-items: center;
    img {
      margin-right: 15px;
    }
  }
  &__temp {
    font-size: 130px;
    font-weight: 100;
  }
  &__temps {
    font-size: 20px;
    margin-right: 20px;
    transform: translateY(-30px);
    color: #fff;
    text-shadow: 1px 2px 2px #333;
    z-index: 0;
  }
  &__maxtemp {
    padding-bottom: 5px;
    border-bottom: 1px solid #fff;
  }
  &__mintemp {
    padding-top: 5px;
  }
}