.search {
  width: 100%;
  position: relative;
  &.open {
    .search__droplist {
      display: block;
    }
    .search__backdrop {
      display: block;
    }
  }
  &__bar {
    position: absolute;
    background-color: #fff;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    z-index: 500;
    box-shadow: 1px 2px 3px #333;
    .menu__icon, .search__icon {
      padding: 5px 10px;
      user-select: none;
      &:active {
        background-color: rgba(0, 0, 0, 0.2);
      }
    }
  }

  &__input {
    width: calc(100% - 100px);
    height: 38px;
    background-color: transparent;
    border: none;
    font-size: 18px;
    font-weight: 500;
    &:focus {
      outline: none;
    }
  }
  &__droplist {
    display: none;
    box-shadow: 1px 2px 3px #333;
    position: absolute;
    left: 0;
    right: 0;
    margin: 0 auto;
    top: 37px;
    background-color: #fff;
    z-index: 1;
    max-height: 300px;
    overflow-y: auto;
    ul {
      list-style: none;
      margin: 0;
      padding: 0;
      li {
        display: flex;
        justify-content: space-between;
        padding: 1rem;
        border-top: 1px solid rgb(218, 218, 218);
        font-size: 16px;
        color: #000;
        font-weight: 500;
        span {
          font-weight: 400;
          font-size: 14px;
        }
        &:active {
          background-color: rgba(0, 0, 0, 0.2);
        }
      }
    }
  }
  &__backdrop {
    display: none;
    position: fixed;
    z-index: 0;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5);
  }
}
